'use client';

import { useEffect, useState, use } from 'react';
import dynamic from 'next/dynamic';
import Link from 'next/link';
import {
    MapPin,
    Calendar,
    Target,
    ChevronLeft,
    Share2,
    Download,
    RefreshCw,
    Trophy,
    Filter,
    List
} from 'lucide-react';
import { Card, Button, Badge, Progress } from '@/components/ui';

// Dynamically import Map component to avoid SSR issues with Leaflet
const MapComponent = dynamic(() => import('@/components/ui/Map'), {
    ssr: false,
    loading: () => (
        <div className="h-full w-full flex items-center justify-center bg-gray-100">
            <RefreshCw className="w-8 h-8 text-gray-400 animate-spin" />
        </div>
    ),
});

interface Result {
    id: string;
    lat: number;
    lng: number;
    rank: number | null;
}

interface Scan {
    id: string;
    keyword: string;
    status: string;
    gridSize: number;
    radius: number;
    frequency: string;
    createdAt: string;
    centerLat: number;
    centerLng: number;
    results: Result[];
}

export default function ScanReportPage({ params }: { params: Promise<{ id: string }> }) {
    const resolvedParams = use(params);
    const [scan, setScan] = useState<Scan | null>(null);
    const [loading, setLoading] = useState(true);
    const [activeTab, setActiveTab] = useState<'map' | 'list'>('map');

    useEffect(() => {
        const fetchScan = () => {
            fetch(`/api/scans/${resolvedParams.id}`)
                .then(res => res.json())
                .then(data => setScan(data.scan))
                .catch(console.error)
                .finally(() => setLoading(false));
        };

        fetchScan();

        // Simple polling
        const interval = setInterval(() => {
            if (scan?.status === 'RUNNING' || scan?.status === 'PENDING') {
                fetchScan();
            }
        }, 5000);

        return () => clearInterval(interval);
    }, [resolvedParams.id, scan?.status]);

    if (loading) {
        return (
            <div className="flex items-center justify-center min-h-screen">
                <Loader />
            </div>
        );
    }

    if (!scan) return <div>Scan not found</div>;

    const totalPoints = scan.gridSize * scan.gridSize;
    const completedPoints = scan.results.length;

    // Stats
    const top3Count = scan.results.filter(r => r.rank && r.rank <= 3).length;
    const top10Count = scan.results.filter(r => r.rank && r.rank > 3 && r.rank <= 10).length;
    const avgRank = scan.results.reduce((acc, r) => acc + (r.rank || 20), 0) / (completedPoints || 1); // Mock avg calculation

    return (
        <div className="max-w-[1600px] mx-auto space-y-6">
            {/* Breadcrumb & Actions */}
            <div className="flex items-center justify-between">
                <div className="flex items-center gap-4">
                    <Link href="/scans" className="w-8 h-8 rounded-full bg-white border border-gray-200 flex items-center justify-center hover:bg-gray-50 transition-colors">
                        <ChevronLeft size={16} className="text-gray-600" />
                    </Link>
                    <div>
                        <h1 className="text-2xl font-bold text-gray-900 flex items-center gap-3">
                            {scan.keyword}
                            <Badge variant={scan.status === 'COMPLETED' ? 'success' : 'blue'}>{scan.status}</Badge>
                        </h1>
                        <p className="text-sm text-gray-500 flex items-center gap-4 mt-1">
                            <span className="flex items-center gap-1"><MapPin size={12} /> Global Coordinates: {scan.centerLat.toFixed(3)}, {scan.centerLng.toFixed(3)}</span>
                            <span className="flex items-center gap-1"><Calendar size={12} /> {new Date(scan.createdAt).toDateString()}</span>
                        </p>
                    </div>
                </div>
                <div className="flex items-center gap-2">
                    <Button variant="outline" size="sm"><Download size={14} className="mr-2" /> PDF Report</Button>
                    <Button variant="outline" size="sm"><Share2 size={14} className="mr-2" /> Share Link</Button>
                </div>
            </div>

            {/* KPI Cards */}
            <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
                <Card className="p-4 flex items-center gap-4">
                    <div className="w-10 h-10 rounded-full bg-green-100 flex items-center justify-center text-green-700">
                        <Trophy size={20} />
                    </div>
                    <div>
                        <p className="text-sm text-gray-500 font-medium">Avg Ranking</p>
                        <p className="text-2xl font-bold text-gray-900">#{activeTab === 'map' ? Math.round(avgRank) : '-'}</p>
                    </div>
                </Card>
                <Card className="p-4 flex items-center gap-4">
                    <div className="w-10 h-10 rounded-full bg-blue-100 flex items-center justify-center text-blue-700">
                        <Target size={20} />
                    </div>
                    <div>
                        <p className="text-sm text-gray-500 font-medium">SoLV score</p>
                        <p className="text-2xl font-bold text-gray-900">
                            {completedPoints > 0 ? Math.round((top3Count / completedPoints) * 100) : 0}
                        </p>
                    </div>
                </Card>
                <Card className="p-4 flex flex-col justify-center">
                    <div className="flex justify-between text-sm mb-1">
                        <span className="text-gray-500">Top 3 Rankings</span>
                        <span className="font-bold text-green-600">{top3Count}</span>
                    </div>
                    <Progress value={top3Count} max={completedPoints || 1} size="sm" color="green" />
                </Card>
                <Card className="p-4 flex flex-col justify-center">
                    <div className="flex justify-between text-sm mb-1">
                        <span className="text-gray-500">4-10 Rankings</span>
                        <span className="font-bold text-yellow-600">{top10Count}</span>
                    </div>
                    <Progress value={top10Count} max={completedPoints || 1} size="sm" color="yellow" />
                </Card>
            </div>

            {/* Main Content Area */}
            <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
                {/* Main Map/List View */}
                <div className="lg:col-span-2 space-y-4">
                    <Card noPadding className="overflow-hidden min-h-[600px] flex flex-col">
                        {/* Toolkit Bar */}
                        <div className="p-3 border-b border-gray-100 bg-gray-50 flex justify-between items-center">
                            <div className="flex gap-1 bg-gray-200 p-1 rounded-lg">
                                <button
                                    onClick={() => setActiveTab('map')}
                                    className={`px-3 py-1.5 text-xs font-semibold rounded-md transition-all ${activeTab === 'map' ? 'bg-white text-gray-900 shadow-sm' : 'text-gray-600 hover:text-gray-800'}`}
                                >
                                    Map View
                                </button>
                                <button
                                    onClick={() => setActiveTab('list')}
                                    className={`px-3 py-1.5 text-xs font-semibold rounded-md transition-all ${activeTab === 'list' ? 'bg-white text-gray-900 shadow-sm' : 'text-gray-600 hover:text-gray-800'}`}
                                >
                                    List View
                                </button>
                            </div>
                            <div className="flex items-center gap-2">
                                <Button variant="ghost" size="sm" className="h-8 text-xs"><Filter size={12} className="mr-1" /> Filter Map</Button>
                            </div>
                        </div>

                        <div className="flex-1 relative bg-gray-100">
                            {activeTab === 'map' ? (
                                <MapComponent
                                    center={[scan.centerLat, scan.centerLng]}
                                    zoom={12}
                                    points={scan.results || []}
                                    gridSize={scan.gridSize}
                                />
                            ) : (
                                <div className="p-8 text-center text-gray-500">
                                    <List size={48} className="mx-auto mb-4 text-gray-300" />
                                    <p>List view of rankings coming soon.</p>
                                </div>
                            )}
                        </div>
                    </Card>
                </div>

                {/* Sidebar Stats */}
                <div className="space-y-6">
                    <Card>
                        <h3 className="font-bold text-gray-900 mb-4 border-b border-gray-100 pb-3">Competitor Summary</h3>
                        <div className="text-center py-8">
                            <p className="text-gray-500 text-sm">Competitor benchmarking features are available in the next update.</p>
                            <Button variant="outline" size="sm" className="mt-4" disabled>Compare Competitors</Button>
                        </div>
                    </Card>

                    <Card>
                        <h3 className="font-bold text-gray-900 mb-4 border-b border-gray-100 pb-3">Location Details</h3>
                        <div className="space-y-3 text-sm">
                            <div className="flex justify-between">
                                <span className="text-gray-500">Grid Radius</span>
                                <span className="font-medium text-gray-800">{scan.radius} km</span>
                            </div>
                            <div className="flex justify-between">
                                <span className="text-gray-500">Grid Points</span>
                                <span className="font-medium text-gray-800">{totalPoints}</span>
                            </div>
                            <div className="flex justify-between">
                                <span className="text-gray-500">Top Rank</span>
                                <span className="font-bold text-green-600">#1</span>
                            </div>
                        </div>
                    </Card>
                </div>
            </div>
        </div>
    );
}

function Loader() {
    return <RefreshCw className="animate-spin text-gray-400" />
}
